  /* 
  SOCIAL ICON 
  -------------------------------------------*/
  .social-icons {
      margin-top: 20px;
      margin-left: 120px;

    }
    @media only screen and (max-width: 500px) {
      .social-icons {
        margin-top: 30px;
        margin-left:60px;

      }
    }

@media only screen and (max-width:384px) {
      .social-icons {
        margin-top: 50px;
        margin-left:40px;

      }
    }

    .social-icons a i{
      position: relative;
      color: #fff;
      font-size: 18px;
      margin: 0 7px;
      line-height: 40px;
      text-align: center;
      width: 40px;
      height: 40px;
    }
    .social-icons a i::before {
      position: relative;
      z-index: 1;
    }
    
    .social-icons a i::after {
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      width: 40px;
      height: 40px;
      border: 1px solid #fff;
      border-radius: 3px;
      -webkit-transform: rotate(45deg);
         -moz-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
           -o-transform: rotate(45deg);
              transform: rotate(45deg);
      -webkit-transition: all 300ms;
         -moz-transition: all 300ms;
           -o-transition: all 300ms;
              transition: all 300ms;            
    }
    
    .social-icons a i:hover::after{
      background: #fd6131ed;
      border: 1px solid transparent;
      z-index: 0;
    }
    .list-inline>li {
      display: inline-block;
      padding-right: 5px;
      padding-left: 5px;
  }

  